.titulo {
  position: relative;
  text-align: center;
  height: 150px;
  line-height: 150px;
  vertical-align: middle;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1; }
  h1 {
    color: $white; } }
