// Button
$button-color-background-primary: linear-gradient(90deg, $primary-color-light 0%, $primary-color 100%) !default;
$button-color-background-primary-hover: $primary-color;

$button-color-background-outline-dark: color('shades', 'transparent') !default;
$button-color-background-outline-dark-hover: $secondary-color-dark;
$button-border-outline-dark: 2px solid $secondary-color-dark;
$button-color-outline-dark: $secondary-color-dark;


$button-color-background-outline-clear: color('shades', 'transparent') !default;
$button-color-background-outline-clear-hover: $white;
$button-color-outline-clear: $secondary-color-dark;

$button-color-background-thinner: color('shades', 'transparent') !default;
$button-color-background-thinner-hover: color('grey', 'lighten-4') !default;
$button-color-thinner: $secondary-color;

$button-color-alert: $error-color;

// Card
$card-color: color('brown','base') !default;
$card-border: 1px solid color('grey', 'lighten-2') !default;
$card-bg: color('shades','white');
$card-color-icon: color('grey', 'darken-1');
// $card-text: color('brown', 'darken')


// Code
$code-color-background: color('blue','darken-1') !default;

// Container

// Error Page

// Footer

// Forms

// Header
$header-color-background: color('blue','darken-1') !default;
$header-color: color('shades', 'white') !default;
$header-border: 1px solid color('grey', 'darken-3');
$header-color-span: color('grey', 'darken-1') !default;

// Modal
$modal-shadow: 0px 4px 8px rgba(color('blue','darken-1'),0.1);
$modal-overlay: rgba(color('blue','darken-1'),0.8) !default;
$modal-color-icon: color('grey', 'lighten-3') !default;

// Preload
$preload-color-background: color('blue','darken-1') !default;
$preload-border-width: 7px !default;
$preload-border-donut: $preload-border-width solid rgba($white, 1);
$preload-border-donut-left: $preload-border-width solid $primary-color;
$preload-border-spinner: ($preload-border-width + 2px) solid $transparent;
$preload-border-spinner-top: ($preload-border-width + 2px) solid color('grey','lighten-1');
$preload-border-circle: $preload-border-width solid rgba($white, 1);

// Progress
$progress-color-background: color('grey', 'lighten-3');
$progress-color-number: color('blue','darken-1');
$progress-bar-color: color('purple','base');


// Sidenav
$sidenav-color-background: color('shades', 'white') !default;
$sidenav-color-background-hover: lighten($secondary-color, 45%) !default;
$sidenav-shadow: 1px 2px 2px rgba(color('blue','darken-1'), 0.1) !default;
$sidenav-color: color('grey', 'darken-3') !default;
$sidenav-color-icon: color('grey','darken-1') !default;
$sidenav-color-icon-hover: $secondary-color !default;


// Table
$table-color-background: $white;
$table-shadow: 1px 2px 2px rgba($black,.1);
$table-border: 1px solid color('grey','lighten-2');
$table-a: color('grey','darken-3') !default;
$table-color-a: color('blue', 'base') !default;
// State

// Comming Soon
$coming-soon-color: color('blue','darken-1');
$coming-soon-background: color('grey','darken-2');


// Disabled
$disabled-no-drop: color('grey','darken-1');

$spacer: 1em !default;
// Font-family to use in the whole project


